        this.innerVisible = true;
        this.innerTitle = item.name;
        for(let i =0 , obj; obj = this.InnerTableInfo[i++];){
          if(obj.id == item.id && obj.parentId == item.parentId){
              for( let key in this.formInnerInline){
                this.formInnerInline[key] = obj[key];
