<template>
<div >
  <button>go</button>
  <!-- <parking-autocomplete></parking-autocomplete> -->
  <parking-table :columnData="columnData" :tableData="tableData"></parking-table>
  </div>
</template>

<script>
  import parkingTable from '../../base/parking-table/parking-table'
  import parkingAutocomplete from '../../base/parking-autocomplete/parking-autocomplete'

  import {config} from '../../config'
  export default {
    data() {
      return {
        restaurants: [],
        state: '',
        timeout:  null,
        columnData:[
          {
            "prop":"name",
            "label":"",
            "width":"180"
          },
          {
            "prop":"date",
            "label":"",
            "width":"180"
          },
          {
            "prop":"address",
            "label":"",
            "width":"180"
          }, 
        ],
        tableData: [{
          date: '2016-05-03',
          name: '1',
          address: ' 1511 '
        }, {
          date: '2016-05-02',
          name: '2',
          address: ' 1512 '
        }, {
          date: '2016-05-04',
          name: '3',
          address: ' 1513 '
        }, {
          date: '2016-05-01',
          name: '4',
          address: ' 1514 '
        }, {
          date: '2016-05-08',
          name: '5',
          address: ' 1515 '
        }, {
          date: '2016-05-06',
          name: '6',
          address: ' 1516 '
        }, {
          date: '2016-05-07',
          name: '7',
          address: ' 1517 '
        }]
      }
    },
    components: {
      parkingAutocomplete,
      parkingTable
      
    },
    created(){

      this.$axios.get('https://pagead2.googlesyndication.com/getconfig/sodar',{       // url
        params:{
            sv:200,
            tid:"gda",
            tv:"r20200817",
            st:"env"
        }
    }).then(function(res){
        console.log(res.data);
    }).catch(function (error) {
        console.log(error);
    });
    // this.$axios.get(`${config.base_url}parkinginfo/getdata`,{       // url
    //     params:{
    //         id:'4537875645645696'
    //     }
    // }).then(function(res){
    //     console.log(res.data);
    // }).catch(function (error) {
    //     console.log(error);
    // });
      
    },
     mounted() {
      //  this.loadAll();
    },
    methods:{
        getRxJSData() {
          return new Observable((observer) => {
            setTimeout( () => {
              observer.next('Rxjs');
              // observer.error('...');
            }, 1000);
    });
  },
  go(){
    this.getRxJSData().subscribe((data) =>{
      console.log(data);
    })
  }
    
    }

  }
</script>
<style scoped>


</style>


