                      console.log(`${Math.ceil(e.srcElement.parentNode.offsetLeft/e.srcElement.parentNode.parentNode.clientWidth*100)}%,${Math.ceil(e.srcElement.parentNode.offsetTop/e.srcElement.parentNode.parentNode.clientHeight*100)}%`)

                          //
                          document.onmousemove = null;
                         //  
                        document.onmouseup = null;
                    };
                };
            },
             
           
